*{
font-family: Arial, Helvetica, sans-serif;
font-size: 1em;
}

body{
  padding: 60px;
}

input{
  color: gray;
  padding: 5px;
  width: 100%;
  font-size: .9rem;  
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;  
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;  
  margin-bottom: 10px;
  margin-right: 10px ;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}